.App {
  text-align: center;
  background-image: linear-gradient(180deg, #090f1b, rgba(23, 30, 45, 0.89)),
    url("https://www.brewsterwallcovering.com/images/thumbs/0024106_mythic-seafoam-floral-wallpaper_360.jpeg");
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  /* background-image: linear-gradient(180deg, #090f1b, rgba(23, 30, 45, 0.89)),
    url("https://www.brewsterwallcovering.com/images/thumbs/0024106_mythic-seafoam-floral-wallpaper_360.jpeg");
  min-width: 100vw; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #6192fb;
  text-decoration: none;
  font-size: 16px;
}

.App-link:hover {
  color: #273e6e;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
